<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <!---->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!---->
      <el-table
        :data="orderList"
        style="width: 100%" border stripe>
        <el-table-column type="index" label="#" width="50"></el-table-column>
        <el-table-column label="" prop="order_number" width="300"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="" prop="order_pay">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.order_pay ==='1'" type="success"></el-tag>
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send" width="100"></el-table-column>
        <el-table-column label="" prop="create_time" width="300"></el-table-column>
        <el-table-column label="" prop="order_pay">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"></el-button>
            <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressbox"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--      -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5,10, 20, 30, 40]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

    <!---->
    <el-dialog
      title=""
      :visible.sync="addressVisible"
      @close="addressDialogClosed"
      width="50%">
      <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px"
               class="demo-ruleForm">
        <el-form-item label="/" prop="address1">
          <el-cascader :options="cityData" v-model="addressForm.address1"
                       :props="{ expandTrigger: 'hover' }"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="addressVisible = false"> </el-button>
    <el-button type="primary" @click="addressVisible = false"> </el-button>
  </span>
    </el-dialog>

    <!---->
    <el-dialog
      title=""
      :visible.sync="progressVisible"
      width="50%"
    >
      <el-timeline :reverse="false">
        <el-timeline-item
          v-for="(activity, index) in progressDataList"
          :key="index"
          :color="'#46a0fc'"
          :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">

  </span>
    </el-dialog>

  </div>
</template>

<script>
  import cityData from "./citydata";

  export default {
    data() {
      return {
        queryInfo: {
          query: "",
          pagenum: 1,
          pagesize: 5
        },
        //
        orderList: [],
        total: 0,
        addressVisible: false,
        addressForm: {
          address1: [],
          address2: ""
        },
        addressFormRules: {
          address1: [{
            required: true,
            message: "/",
            trigger: "blur"
          }], address2: [{
            required: true,
            message: "",
            trigger: "blur"
          }]
        },
        cityData,
        progressVisible: false,
        //
        progressDataList: []

      };
    },
    created() {
      this.getOrderList();
    },
    methods: {
      async getOrderList() {
        const {data: res} = await this.$http.get(`orders`, {params: this.queryInfo});
        if (res.meta.status !== 200) return this.$message.error("");
        // console.log(res);
        res.data.goods.forEach(item => {
          // console.log(item.create_time);
          item.create_time = this.toDate(item.create_time + 1000);
        });
        this.orderList = res.data.goods;
        this.total = res.data.total;
        this.$message.success("");
      },
      //  
      toDate(dates) {
        dates = new Date(dates);
        let year = dates.getFullYear();
        //str.padStart(),
        let mon = (dates.getMonth() + 1 + "").padStart(2, "0");
        let date = (dates.getDate() + "").padStart(2, "0");
        let hour = (dates.getHours() + "").padStart(2, "0");
        let min = (dates.getMinutes() + "").padStart(2, "0");
        let sec = (dates.getSeconds() + "").padStart(2, "0");
        return `${year}-${mon}-${date} ${hour}:${min}:${sec}`;
      },
      //pagesize--
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize;
        this.getOrderList();
      },
      //pagenum--
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage;
        this.getOrderList();
      },
      //
      showBox() {
        this.addressVisible = true;
      },
      //
      addressDialogClosed() {
        this.$refs.addressFormRef.resetFields();
      },
      async showProgressbox() {
        const {data: res} = await this.$http.get(`/kuaidi/1106975712662`);
        if (res.meta.status !== 200) return this.$message.error("");
        console.log(res);
        this.progressDataList = res.data;
        this.progressVisible = true;
      }
    }
  };
</script>

<style lang='less' scoped>
  .el-cascader {
    width: 100%;
  }
</style>
