<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card" shadow="never" body-style="padding-top:10px">
      <div style="margin-top: 15px;">
        <!--  -->
        <el-row :gutter="20">
          <el-col :span="12">
            <!--  -->
            <el-input
              clearable
              @clear="getUserList"
              placeholder=""
              v-model="queryInfo.query"
              class="input-with-select"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="getUserList"
              ></el-button>
            </el-input>
          </el-col>
          <!--  -->
          <el-col :span="4">
            <el-button type="primary" @click="addDialogVisible = true"
              ></el-button
            >
          </el-col>
        </el-row>
      </div>
      <!--  -->
      <el-table :data="userlist" style="width: 100%" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column
          prop="username"
          label=""
          width="180"
        ></el-table-column>
        <el-table-column
          prop="email"
          label=""
          width="180"
        ></el-table-column>
        <el-table-column prop="mobile" label=""></el-table-column>
        <el-table-column prop="role_name" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!-- {{ scope.row.mg_state }} -->
            <el-switch
              v-model="scope.row.mg_state"
              @change="userStateChanged(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <!--  -->
              <el-button
                type="primary"
                icon="el-icon-edit"
                circle
                size="mini"
                @click="showEditDialog(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <!--   -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <!--   -->
              <el-button
                type="warning"
                icon="el-icon-setting"
                circle
                size="mini"
                @click="setRole(scope.row)"
              ></el-button>
            </el-tooltip>

            <!--   -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <!--  -->
              <el-button
                type="danger"
                icon="el-icon-delete"
                circle
                size="mini"
                @click="removeUserById(scope.row.id)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="addFromRules"
        ref="addFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"></el-button>
        <el-button type="primary" @click="addUser"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <!--  -->
      <el-form
        :model="editForm"
        :rules="editFromRules"
        ref="editFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"></el-button>
        <el-button type="primary" @click="editUserInfo()"></el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="setRoledialogVisible" width="50%" @close='setRoleDialogClosed'>
      <div>
        <p>:{{ userInfo.username }}</p>
        <p>:{{ userInfo.role_name }}</p>
        <p>
          
          <el-select v-model="selectedRoleId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRoledialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    // 
    let checkEmail = (rule, value, cb) => {
      const regEmail = /^([a-zA-Z0-9])+@([a-zA-Z0-9_-])+([a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    // 
    let checkMabile = (rule, value, cb) => {
      const regMabile = /^(0|86|17951)?(13[0-9]|15[0-9]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMabile.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }

    return {
      // 
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 2
      },
      userlist: [],
      total: 0,
      mg_state: true,
      input3: '',
      //
      addDialogVisible: false,
      //
      editDialogVisible: false,
      //   
      editForm: {},
      //
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },

      //
      addFromRules: {
        username: [
          {
            required: true,
            message: '',
            trigger: ['blur', 'change']
          },
          {
            min: 3,
            max: 10,
            message: '3-10',
            trigger: ['blur', 'change']
          }
        ],
        password: [
          {
            required: true,
            message: '',
            trigger: ['blur', 'change']
          },
          {
            min: 5,
            max: 15,
            message: '6-15',
            trigger: ['blur', 'change']
          }
        ],
        email: [
          {
            required: true,
            message: '',
            trigger: ['blur', 'change']
          },
          { validator: checkEmail, trigger: ['blur', 'change'] }
        ],
        mobile: [
          {
            required: true,
            message: '',
            trigger: ['blur', 'change']
          },
          { validator: checkMabile, trigger: ['blur', 'change'] }
        ]
      },
      //
      editFromRules: {
        email: [
          {
            required: true,
            message: '',
            trigger: ['blur', 'change']
          },
          { validator: checkEmail, trigger: ['blur', 'change'] }
        ],
        mobile: [
          {
            required: true,
            message: '',
            trigger: ['blur', 'change']
          },
          { validator: checkMabile, trigger: ['blur', 'change'] }
        ]
      },
      // 
      setRoledialogVisible: false,
      // 
      userInfo: {},
      // 
      rolesList: [],
      // id
      selectedRoleId: ''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    //   
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.userlist = res.data.users
      //   
      this.total = res.data.total
      //   this.mg_state=res.data.mg_state
      console.log(res)
    },
    // pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    // 
    async userStateChanged(userInfo) {
      // 
      //   console.log(userInfo.mg_state)
      const { data: res } = await this.$http.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state
        return this.$message.error('')
      } else {
        this.$message.success('')
      }
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 
        const { data: res } = await this.$http.post('users', this.addForm)
        console.log(res.meta.status)

        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    async showEditDialog(id) {
      const { data: res } = await this.$http.get(`users/${id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 
    editDialogClosed() {
      // 
      this.$refs.editFormRef.resetFields()
    },
    editUserInfo() {
      this.$refs.editFormRef.validate(async valid => {
        //  
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.put(
          `users/${this.editForm.id}`,
          {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          }
        )
        console.log(res)

        if (res.meta.status !== 200) this.$message.error('')
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        this.$message.success('')
      })
    },
    async removeUserById(id) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      // cancel
      // confirm
      console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      //
      const { data: res } = await this.$http.delete(`users/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      //
      this.getUserList()
    },
    // 
    async setRole(userInfo) {
      this.userInfo = userInfo
      // 
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.$message.success('')
      this.rolesList = res.data
      console.log(this.rolesList)

      this.setRoledialogVisible = true
    },
    // 
    async saveRoleInfo() {
      if (!this.selectedRoleId) {
        return this.$message.error('')
      }
      console.log(this.selectedRoleId);
      const { data: res } = await this.$http.put(
        `users/${this.userInfo.id}/role`,
        {
          rid:this.selectedRoleId
        }
      )
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getUserList()
      this.setRoledialogVisible = false
    },
    //  
    setRoleDialogClosed(){
      console.log(this.userInfo);  
      this.selectedRoleId = ''
      this.userInfo = {}
    }
  }
}
</script>
<style lang="less" scoped>
.el-card {
  margin-top: 15px;
  font-size: 12px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
}
.el-input {
  margin-bottom: 15px;
}
.el-table {
  font-size: 12px;
}

.body-style {
  padding: 20px;
}
.el-pagination {
  margin-top: 15px;
}
.el-table{
  margin-top: 10px;
}
</style>
