<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"
          >
          </el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <tree-table
        class="treetable"
        :data="catelist"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
      >
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i
            v-if="scope.row.cat_deleted === false"
            class="el-icon-success"
            style="color:lightgreen"
          ></i>
          <i v-else class="el-icon-error" style="color:red"> </i>
        </template>

        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0" size="mini"></el-tag>
          <el-tag
            v-else-if="scope.row.cat_level === 1"
            size="mini"
            type="success">
          </el-tag>
          <el-tag v-else size="mini" type="warning"></el-tag>
        </template>

        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button type="primary" size="mini" icon="el-icon-edit"
          >
          </el-button
          >
          <el-button type="danger" size="mini" icon="el-icon-delete"
          >
          </el-button
          >
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addCatedialogVisible"
      width="50%"
    >
      <el-form
        :model="addCateForm"
        :rules="addCateFromRules"
        ref="addCateFromRef"
        label-width="100px"
      >
        <el-form-item label=":" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <!--  -->
          <el-cascader
            clearable
            change-on-select
            expand-trigger="hover"
            v-model="selectedKeys"
            :options="parentCateList"
            :props="cascaderProps"
            @change="parentCateChanged"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogClosed"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        //
        queryInfo: {
          type: 3,
          //
          pagenum: 1,
          //
          pagesize: 5
        },
        //
        catelist: [],
        //
        total: 0,
        // table
        columns: [
          {
            label: "",
            prop: "cat_name"
          },
          {
            label: "",
            //
            type: "template",
            // 
            template: "isok"
          },
          {
            label: "",
            //
            type: "template",
            // 
            template: "order"
          },
          {
            label: "",
            //
            type: "template",
            // 
            template: "opt"
          }
        ],
        // 
        addCatedialogVisible: false,
        // 
        addCateForm: {
          // 
          cat_name: "",
          // id
          cat_pid: 0,
          // 
          cat_level: 0
        },
        formLabelWidth: "120px",
        // 
        addCateFromRules: {
          cat_name: [
            {required: true, message: "", trigger: "blur"}
          ]
        },
        // 
        parentCateList: [],
        // 
        cascaderProps: {
          value: "cat_id",
          label: "cat_name",
          children: "children",
          expandTrigger: "hover"
        },
        // id
        selectedKeys: []
      };
    },
    created() {
      //
      this.getCateList();
    },
    methods: {
      //
      async getCateList() {
        const {data: res} = await this.$http.get("categories", {
          params: this.queryInfo
        });
        if (res.meta.status !== 200) return this.$message.error("");
        this.$message.success("");
        // catelist
        this.catelist = res.data.result;
        this.total = res.data.total;
      },
      // pagesize
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize;
        // 
        this.getCateList();
      },
      // pagenum
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage;
        // 
        this.getCateList();
      },
      showAddCateDialog() {
        // 
        this.getParentCateList();
        this.addCatedialogVisible = true;
      },
      // 
      async getParentCateList() {
        const {data: res} = await this.$http.get("categories", {
          params: {type: 2}
        });
        if (res.meta.status !== 200) return this.$message.error("");
        this.$message.success("");
        this.parentCateList = res.data;
        console.log(this.parentCateList);
      },
      // 
      parentCateChanged() {
        // console.log(this.selectedKeys)
        // 0 
        if (this.selectedKeys.length > 0) {
          // console.log(this.selectedKeys[this.selectedKeys.length - 1])
          // id
          this.addCateForm.cat_pid = this.selectedKeys[
          this.selectedKeys.length - 1
            ];
          // 
          this.addCateForm.cat_level = this.selectedKeys.length;
          return;
        } else {
          // id0id
          this.addCateForm.cat_pid = 0;
          // 0 
          this.addCateForm.cat_level = 0;
        }
      },
      // 
      addCate() {
        // console.log(this.addCateForm)
        this.$refs.addCateFromRef.validate(async valid => {
          if (!valid) return;
          const {data: res} = await this.$http.post(
            "categories",
            this.addCateForm
          );
          if (res.meta.status !== 201) {
            return this.$message.error("");
          } else {
            this.getCateList();
            this.addCateDialogClosed();
            this.$message.success("");
          }
        });
      },
      // 
      addCateDialogClosed() {
        // 
        this.addCatedialogVisible = false;
        //
        this.$refs.addCateFromRef.resetFields();
        // ;
        this.selectedKeys = [];
        this.addCateForm.cat_level = 0;
        this.addCateForm.cat_pid = 0;
      }
    }
  };
</script>

<style lang="less" scoped>
  .treetable {
    margin-top: 10px;
  }

  .el-cascader {
    width: 100%;
    overflow: hidden;
  }
</style>
