.el-card {
  margin-top: 15px;
}
.el-tag {
  margin: 7px;
}
.el-table{
  margin-top: 10px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
