<template>
  <div>
    <!-- -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--    -->
    <el-card>
      <!---->
      <div class="search">
        <el-input
          clearable
          placeholder=""
          v-model="inputSearch"
          clearable
          @click="deleteQuery"
        >
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="searchGoods"
          ></el-button>
        </el-input>
        <el-button type="primary" @click="goAddpage"></el-button>
      </div>
      <!---->
      <el-table :data="goodsData" stripe border>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="goods_name" width="630px"></el-table-column>
        <el-table-column label="()" prop="goods_price" width="100px"></el-table-column>
        <el-table-column label="" prop="goods_weight" width="80px"></el-table-column>
        <el-table-column label="" prop="add_time" width="150px"></el-table-column>
        <el-table-column label="" prop="">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" circle></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" circle
                       @click="delGoods(scope.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryData.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>


  </div>
</template>

<script>
  export default {
    data: function () {
      return {
        queryData: {
          query: "",
          //
          pagenum: 1,
          //
          pagesize: 10
        },
        //
        goodsData: [],
        //  
        goodsNameList: [],
        //  
        inputSearch: "",
        //  
        total: 0,
        //
        addGoodsdialogVisible: false,
        // 
        queryAddData: {
          goods_name: "",
          goods_cat: [],
          goods_price: "",
          goods_number: "",
          goods_weight: ""
        },
        formLabelWidth: "150"
      };
    },
    created() {
      this.getGoodsList();
    },
    methods: {
      //
      async getGoodsList() {
        const {data: res} = await this.$http.get("/goods", {params: this.queryData});
        if (res.meta.status !== 200) return this.$message.error("");

        console.log(res.data);
        res.data.goods.forEach(item => {
          item.add_time = this.toDate(item.add_time);
        });
        console.log(res.data);
        this.total = res.data.total;

        this.goodsData = res.data.goods;
        console.log(this.goodsData);

      },
      //  
      toDate(dates) {
        dates = new Date(dates);
        let year = dates.getFullYear();
        let mon = dates.getMonth() + 1;
        mon = mon > 9 ? mon : `0${mon}`;
        let date = dates.getDate();
        date = date > 9 ? date : `0${date}`;
        let hour = dates.getHours();
        hour = hour > 9 ? hour : `0${hour}`;
        let min = dates.getMinutes();
        min = min > 9 ? min : `0${min}`;
        let sec = dates.getSeconds();
        sec = sec > 9 ? sec : `0${sec}`;
        console.log(`${year}+${mon}+${date}`);
        return `${year}-${mon}-${date} ${hour}:${min}:${sec}`;
      },
      //  
      searchGoods() {
        this.queryData.query = this.inputSearch;
        this.getGoodsList();
      },
      //  
      deleteQuery() {
        this.inputSearch = "";
        this.queryData.query = this.inputSearch;
        this.getGoodsList();
      },
      handleSizeChange(val) {
        // console.log(` ${val} `);
        this.queryData.pagesize = val;
        this.getGoodsList();
      },
      handleCurrentChange(val) {
        // console.log(`: ${val}`);
        this.queryData.pagenum = val;
        this.getGoodsList();
      },
      goAddpage() {
      this.$router.push('/goods/add')
      },
      //
      async delGoods(id) {
        const confirmResult = await this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).catch(err => err);
        if (confirmResult !== "confirm") return this.$message.info("");
        const {data: res} = await this.$http.delete(`goods/${id}`);
        // console.log(res)
        if (res.meta.status!== 200) return this.$message.info("");
        this.$message.success("");
        this.getGoodsList();
      }

    },
    watch: {
      // 
      inputSearch: function () {
        this.queryData.query = this.inputSearch;
        this.getGoodsList();
      }
    }

  };
</script>

<style scoped lang="less">
  .el-card {
    .search {
      display: flex;
      justify-content: flex-start;

      .el-input {
        width: 500px;
        margin-right: 10px;
      }

      .el-button {
        height: 40px;
      }
    }

    .el-table {
      font-size: 13px !important;

    }

  }

  .num {
    width: 100px !important;
    margin-right: 20px;
  }

  .num-box {
    display: flex;
  }
</style>
