<!--TODO-->
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert
        title=""
        type="warning"
        :closable="false"
        show-icon
      ></el-alert>
      <!--  -->
      <el-row class="cat_opt">
        <el-col>
          <span>:</span>
          <el-cascader
            v-model="selectedCateKeys"
            :options="catelist"
            :props="cateProps"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>

      <!-- tabs -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="" name="many">
          <!--  -->
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
            @click="addDialogVisible = true"
          >
          </el-button
          >
          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable @close="handleClose(scope.row,i)" :disable-transitions="true">
                  {{item}}
                </el-tag>
                <!---->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <!--tag-->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ </el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column
              prop="attr_name"
              label=""
            ></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  icon="el-icon-edit"
                  type="primary"
                  @click="showEditDialog(scope.row.attr_id)"
                >
                </el-button>
                <el-button
                  size="mini"
                  icon="el-icon-delete"
                  type="danger"
                  @click="showDelDialog(scope.row.attr_id)"
                >
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <!--  -->
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
            @click="addDialogVisible = true"
          >
          </el-button
          >
          <!--  -->
          <el-table :data="onlyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable @close="handleClose(scope.row,i)" :disable-transitions="true">
                  {{item}}
                </el-tag>
                <!---->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <!--tag-->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ </el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column
              prop="attr_name"
              label=""
            ></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  icon="el-icon-edit"
                  type="primary"
                  @click="showEditDialog(scope.row.attr_id)"
                >
                </el-button
                >
                <el-button size="mini" icon="el-icon-delete" type="danger"
                           @click="showDelDialog(scope.row.attr_id)"
                >
                </el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!-- - -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>
    <!-- - -->
    <el-dialog
      :title="'' + titleText"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <!--  -->
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>
    <!--- -->
    <el-dialog
      title=""
      :visible.sync="delDialogVisible"
      width="30%"
    >
      <span></span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="delDialogVisible = false"> </el-button>
    <el-button type="primary" @click="delAttr"> </el-button>
  </span>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        //
        catelist: [],
        //
        cateProps: {
          value: "cat_id",
          label: "cat_name",
          children: "children",
          expandTrigger: "hover"
        },
        //
        selectedCateKeys: [],
        //
        activeName: "many",
        //
        manyTableData: [],
        //
        onlyTableData: [],
        //
        addDialogVisible: false,
        //
        addForm: {},
        //
        addFormRules: {
          attr_name: [
            {
              required: "true",
              message: "",
              trigger: "blur"
            }
          ]
        },
        //
        editDialogVisible: false,
        //
        editForm: {},
        //
        editFormRules: {
          attr_name: [
            {
              required: "true",
              message: "",
              trigger: "blur"
            }
          ]
        },
        delDialogVisible: false,
        delAttrId: "",
        inputVisible: false,
        //
        inputValue: ""
      };
    },
    created() {
      this.getCateList();
    },
    methods: {
      //
      async getCateList() {
        const {data: res} = await this.$http.get("categories");
        if (res.meta.status !== 200) return this.$message.error("");
        this.catelist = res.data;
        //console.log(this.catelist)
      },
      // 
      handleChange() {
        this.getParamsData();
      },
      // tab
      handleTabClick() {
        //   console.log(this.activeName)
        this.getParamsData();
      },
      // 
      async getParamsData() {
        //  console.log(this.selectedCateKeys)
        // 
        if (this.selectedCateKeys.length !== 3) {
          this.selectedCateKeys = [];
          //
          this.manyTableData = [];
          this.onlyTableData = []
        } else {
          // id
          const {data: res} = await this.$http.get(
            `categories/${this.cateId}/attributes`,
            {
              params: {sel: this.activeName}
            }
          );
          if (res.meta.status !== 200) return this.$message.error("");
          // console.log(res.data);
          this.$message.success("");
          res.data.forEach(item => {
            // 
            // 
            item.attr_vals = item.attr_vals ? item.attr_vals.split(",") : [];
            //  datainputVisibleinputValue
            //  tagtag
            //  
            item.inputVisible = false;
            //  
            item.inputValue = "";

          });
          console.log(res.data);
          if (this.activeName === "many") {
            this.manyTableData = res.data;
            //   console.log(this.manyTableData)
          } else {
            this.onlyTableData = res.data;
            // console.log(this.onlyTableData)
          }
        }
      },
      // 
      addDialogClosed() {
        // 
        this.$refs.addFormRef.resetFields();
      },
      // 
      addParams() {
        // 
        this.$refs.addFormRef.validate(async valid => {
          if (!valid) return console.log("");
          console.log(this.cateId);
          console.log(this.addForm.attr_name);
          console.log(this.activeName);

          const {data: res} = await this.$http.post(
            `categories/${this.cateId}/attributes`,
            {
              attr_name: this.addForm.attr_name,
              attr_sel: this.activeName
            }
          );
          console.log(res);
          if (res.meta.status !== 201) return this.$message.error("");
          this.$message.success("");
          this.getParamsData();
          this.addDialogVisible = false;
        });
      },
      // 
      async showEditDialog(attr_id) {
        this.editDialogVisible = true;
        const {data: res} = await this.$http.get(
          `categories/${this.cateId}/attributes/${attr_id}`,
          {
            attr_name: this.editForm.attr_name,
            attr_sel: this.activeName
          }
        );
        if (res.meta.status !== 200) return this.$message.error("");
        console.log(res.data.attr_name);
        // 
        this.editForm = res.data;
      },
      // 
      editDialogClosed() {
        // 
        this.$refs.editFormRef.resetFields();
      },
      // 
      editParams() {
        //  
        this.$refs.editFormRef.validate(async valid => {
          if (!valid) return;
          const {data: res} = await this.$http.put(
            `categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
            {
              attr_name: this.editForm.attr_name,
              attr_sel: this.activeName
            }
          );
          if (res.meta.status !== 200) return this.$message.error("");
          this.$message.success("");
          // 
          this.getParamsData();
          //
          this.editDialogVisible = false;
        });
      },
      //
      showDelDialog(delAttrId) {
        this.delAttrId = delAttrId;
        this.delDialogVisible = true;
      },
      //
      async delAttr() {
        const {data: res} = await this.$http.delete(`categories/${this.cateId}/attributes/${this.delAttrId}`);
        if (res.meta.status !== 200) return this.$message.error("");
        this.$message.success("");
        this.delDialogVisible = false;
        this.getParamsData();
        this.delAttrId = "";
      },
      //taginputbtn
      showInput(row) {
        row.inputVisible = true;
        //
        //$nextTick
        //inputinput
        this.$nextTick(() => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },
      //enter
      async handleInputConfirm(row) {
        // trim
        let inputValue = row.inputValue.trim();
        if (inputValue.length === 0) {
          row.inputVisible = false;
          row.inputValue = "";
          return 0;
        }
        /*
        * 
        * attr_vals
        * */
        row.attr_vals.push(inputValue);
        //inputinput
        row.inputValue = "";
        row.inputVisible = false;
        this.saveAttrVals(row);
      },
      async saveAttrVals(row) {
        //
        const {data: res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          //
          attr_vals: row.attr_vals.join(",")
        });
        if (res.meta.status !== 200) {
          return this.$message.error("");
        } else {
          this.$message.success("");
        }
      },
      //tag,
      handleClose(row, i) {
        console.log(row);
        row.attr_vals.splice(i, 1);
        // this.getParamsData();
        // this.$message.success("");
        this.saveAttrVals(row);
      }
    },


    //
    computed: {
      // true
      isBtnDisabled() {
        if (this.selectedCateKeys.length !== 3) {
          return true;
        } else {
          return false;
        }
      }
      ,
      //   id
      cateId() {
        if (this.selectedCateKeys.length === 3) {
          return this.selectedCateKeys[2];
        } else {
          return null;
        }
      }
      ,
      // 
      titleText() {
        if (this.activeName === "many") {
          return "";
        } else {
          return "";
        }
      }
    }
  }
  ;
</script>

<style lang="less" scoped>
  .cat_opt {
    margin: 15px 0;

    .el-cascader {
      margin: 0 15px;
      width: 400px;
    }
  }

  .el-table {
    margin-top: 15px;
  }

  .el-tag {
    margin: 5px;
  }

  /**/
  .input-new-tag {
    width: 120px;
  }
</style>
