<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card>
      <el-alert
        title=""
        type="info"
        effect="dark"
        center
        :closable=false
        show-icon>
      </el-alert>

      <!--      -->
      <el-steps :active="activeIndex - 0" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!--tab-->
      <!--l-tableel-tabs-->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-tabs v-model="activeIndex" tab-position="left" :before-leave="beforeTabLeave" @tab-click="tabClicked">
          <el-tab-pane label="" name="0">
            <el-form-item prop="goods_name" label="">
              <el-input v-model="addForm.goods_name" class="el-new-input"></el-input>
            </el-form-item>
            <el-form-item prop="goods_price" label="">
              <el-input v-model="addForm.goods_price" type="number" class="el-new-input"></el-input>
            </el-form-item>
            <el-form-item prop="goods_number" label="">
              <el-input v-model="addForm.goods_number" type="number" class="el-new-input"></el-input>
            </el-form-item>
            <el-form-item prop="goods_weight" label="">
              <el-input v-model="addForm.goods_weight" type="number" class="el-new-input"></el-input>
            </el-form-item>
            <el-form-item prop="" label="">
              <el-cascader
                class="input-new-tag"
                v-model="addForm.goods_cat"
                :options="catelist"
                :props="cateProps"
                @change="handleChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!--item---->
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <!---->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="cb" v-for="(cb,i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <!--item---->
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id" label-width="150px">
              <el-input v-model="item.attr_vals" class="el-new-input"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!-----action-->
            <el-upload
              :action="uploadUrl"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :headers="headerObj"
              :on-success="handleSuccess"
              list-type="picture">
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!---->
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <!-- -->
            <el-button type="primary" class="btnAdd" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!---->
    <el-dialog
      title=""
      :visible.sync="previewVisible"
      width="50%"
    >
      <img :src="previewPath" class="preview-img">
    </el-dialog>
  </div>
</template>

<script>
  //lodash
  import _ from "lodash";

  export default {
    data() {
      return {
        activeIndex: "0",
        //
        addForm: {
          goods_name: "",
          goods_price: 0,
          goods_number: 0,
          goods_weight: 0,
          //
          goods_cat: [],
          //  
          pics: [],
          //--
          goods_introduce: "",
          attrs: []
        },
        //
        addFormRules: {
          goods_name: [
            {
              required: true, message: "", trigger: "blur"
            }
          ], goods_price: [
            {
              required: true, message: "", trigger: "blur"
            }
          ], goods_number: [
            {
              required: true, message: "", trigger: "blur"
            }
          ], goods_weight: [
            {
              required: true, message: "", trigger: "blur"
            }
          ]
        },
        //
        catelist: [],
        cateProps: {
          label: "cat_name",
          value: "cat_id",
          children: "children",
          expandTrigger: "hover"
        },
        //---
        manyTableData: [],
        //---
        onlyTableData: [],
        //
        uploadUrl: "http://127.0.0.1:8888/api/private/v1/upload",
        //headers
        headerObj: {
          Authorization: window.sessionStorage.getItem("token")
        },
        //
        previewPath: "",
        //
        previewVisible: false
      };
    },
    created() {
      this.getCateList();
    },
    methods: {
      //
      async getCateList() {
        const {data: res} = await this.$http.get("categories");
        if (res.meta.status !== 200) return this.$message.error("");
        this.catelist = res.data;
      },
      //
      handleChange() {
        console.log(this.addForm.goods_cat);
        if (this.addForm.goods_cat.length !== 3) {
          // 
          this.addForm.goods_cat = [];
        } else {

        }
      },
      // false
      beforeTabLeave(activeName, oldActiveName) {
        // console.log("" + oldActiveName);
        // console.log("" + activeName);
        if (!this.addForm.goods_name) {
          this.$message.error("");
          return false;
        }
        if (oldActiveName === "0" && this.addForm.goods_cat.length !== 3) {
          this.$message.error("");
          return false;
        }
      },
      //tab
      async tabClicked() {
        //---
        if (this.activeIndex === "1") {
          // console.log('');
          const {data: res} = await this.$http.get(`categories/${this.cateId}/attributes`, {params: {sel: "many"}});
          if (res.meta.status !== 200) return this.$message.error("");
          res.data.forEach(item => {
            //   
            item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(",");
          });
          this.manyTableData = res.data;
          // console.log(this.manyTableData.attr_vals);
          // console.log(this.manyTableData);
        } else if (this.activeIndex === "2") {
          //  ---
          const {data: res} = await this.$http.get(`categories/${this.cateId}/attributes`, {params: {sel: "only"}});
          if (res.meta.status !== 200) return this.$message.error("");
          this.onlyTableData = res.data;
        }
      },
      //
      handleRemove(file) {
        //console.log(file);
        //1.
        const filePath = file.res.data.tmp_path;
        //2.pics
        const i = this.addForm.pics.findIndex(x => {
          x.pic === filePath;
        });
        //3.splicepics
        this.addForm.pics.splice(i, 1);
      },
      //
      handlePreview(file) {
        //url
        this.previewPath = file.response.data.url;
        console.log(this.previewPath);
        this.previewVisible = true;
      },
      //
      handleSuccess(res) {
        // console.log(res);
        //1.
        const picInfo = {pic: res.data.tmp_path};
        //2.pushpics
        this.addForm.pics.push(picInfo);
        console.log(this.addForm.pics);
      },
      //
      add() {
        this.$refs.addFormRef.validate(async (valid) => {
          if (!valid) return this.$message.error("");
          console.log(valid);
          //
          /*
          * goods_cat
          * addFormgoods_catgoods_cat
          * lodash
          *
          * JSON.parse(JSON.stringify(obj))....
          * ...
          *
          * */
          //
          const form = _.cloneDeep(this.addForm);
          form.goods_cat = form.goods_cat.join(",");

          //
          this.manyTableData.forEach(item => {
            const newInfo = {
              attr_id: item.attr_id,
              attr_value: item.attr_vals.join(",")
            };
            this.addForm.attrs.push(newInfo);
          });
          //  
          this.onlyTableData.forEach(item => {
            const newInfo = {
              attr_id: item.attr_id,
              //attr_vals
              attr_value: item.attr_vals
            };
            this.addForm.attrs.push(newInfo);
          });
          form.attrs = this.addForm.attrs;
          // console.log(this.addForm);
          console.log(form);

          //  --
          //  
          const {data: res} = await this.$http.post("goods", form);
          if (res.meta.status !== 201) {
            console.log(res);
            return this.$message.error(`${res.meta.msg}`);
          }
          this.$message.success("");
          this.$router.push("/goods");
        });

      }

    },
    computed: {
      //---id
      cateId() {
        if (this.addForm.goods_cat.length === 3) {
          return this.addForm.goods_cat[2];
        }
        return null;
      }
    }
  }
  ;
</script>

<style scoped lang="less">
  .el-card {
    .el-alert {
      margin-bottom: 15px;
    }

    .el-step {
      margin-bottom: 25px;
    }

    .el-new-input {
      width: 500px;
    }

    .input-new-tag {
      width: 500px;
    }

    .el-checkbox {
      margin: 0 10px 0 0 !important;
    }

  }

  .preview-img {
    width: 100%;
  }

  .btnAdd {
    margin-top: 15px;
  }

</style>
